import React, { useContext } from 'react';
import { GlobalContext } from '../context/GlobalState';
import styled from 'styled-components';

const DeleteButton = styled.button`
    cursor: pointer;
    transform: translateX(100%);
    height: 100%;
    width: 8%;
    transition: .3s;
    border: none;
    border-left: 1px solid lightgrey;
    width: 0;
`

const ElementInfo = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    transition: 0.3s;
    padding: 3%;
    padding-left: 5%;
`

const ListElement = styled.li`
    display: flex;
    height: 40px;
    align-items: center;
    border: 1px solid lightgrey;
    border-left: ${props => (props.plus === 'plus' ? '4px solid green' : '4px solid red')};
    margin: 10px 0;
    box-sizing: border-box;
    background-color: white;
    &:hover ${DeleteButton} {
        transform: translateX(0);
        width: 30px;
    }
`


export const Transaction = ({ transaction }) => {
    const { deleteTransaction } = useContext(GlobalContext);
    const sign = transaction.amount < 0 ? '-' : '+';
    return (
        <ListElement plus={transaction.amount >= 0 ? 'plus' : ''}>
            <ElementInfo>
                {transaction.text} 
                <span>
                    {sign}${Math.abs(transaction.amount)}
                </span>
            </ElementInfo>
            <DeleteButton onClick={() => deleteTransaction(transaction.id)}>X</DeleteButton>
        </ListElement>
    )
}
