import React, {useState, useContext} from 'react'
import { GlobalContext } from '../context/GlobalState';
import styled from 'styled-components';

const Input = styled.input`
    display: block;
    width: 100%;
    min-height: 30px;
    padding: 0 7px;
    margin: 1vh auto;
    font-size: 16px;
    box-sizing: border-box;
`

const Button = styled.button`
    width: 100%;
    display: block;
    margin: 0 auto;
    height: 3vh;
    min-height: 40px;
    color: white;
    background-color: blue;
    cursor: pointer;
    font-weight: bold;
    border: none;
`

const Title = styled.h3`
    border-bottom: 1px solid lightgrey;
    padding-bottom: 8px;
`

export const AddTransaction = () => {
    const [text, setText] = useState('');
    const [amount, setAmount] = useState(0);

    const { addTransaction } = useContext(GlobalContext);

    const onSubmit = e => {
        e.preventDefault();

        const newTransaction = {
            id: Math.floor(Math.random() * 10000000000),
            text,
            amount: +amount
        }

        addTransaction(newTransaction);
    }

    return (
        <>
            <Title>Add new transaction</Title>
            <form onSubmit={onSubmit}>
                <div>
                    <label htmlFor="text">Text</label>
                    <Input type="text" id="textInput" value={text} onChange={(e) => setText(e.target.value)} placeholder="Enter text..." />
                </div>
                <div>
                    <label htmlFor="amount">Amount</label>
                    <Input type="number" id="amountInput" value={amount} onChange={(e) => setAmount(e.target.value)} placeholder="Enter amount..."/>
                </div>
                <Button>Add transaction</Button>
            </form>
        </>
    )
}
